@use '../../../../../styles/variables' as *;

/* 
   MEMBER CHIP  image card with hover popup
    */

$gold: #c9a84c;
$gold-dark: #a8872a;
$crimson: #a0001b;
$crimson-bright: #c41e3a;
$ink: #2c2218;
$ink-mid: #6b5d4a;
$ink-faint: #9a8b76;

.mchip {
  position: relative;
  flex: 1;
  flex-shrink: 0;
  min-width: 170px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 0 2rem;
  background: linear-gradient(180deg,
      color-mix(in srgb, var(--chip-accent, $gold-dark) 14%, transparent) 0%,
      color-mix(in srgb, var(--chip-primary, $gold) 8%, transparent) 50%,
      transparent 100%);
  cursor: default;
  transition: opacity 0.2s, filter 0.2s;
  // NO overflow hidden here  popup must be visible

  /*  Battle states  */
  &--attacker {
    .mchip__frame {
      box-shadow:
        0 0 0 2px var(--chip-primary, $gold),
        0 0 20px color-mix(in srgb, var(--chip-primary, $gold) 35%, transparent);
    }
  }

  &--defender {
    .mchip__frame {
      box-shadow:
        0 0 0 2px $crimson-bright,
        0 0 16px rgba($crimson-bright, 0.3);
    }

    .mchip__bg {
      filter: brightness(0.85);
    }
  }

  &--spotlight {
    .mchip__frame {
      box-shadow:
        0 0 0 2px var(--chip-primary, $gold),
        0 0 24px color-mix(in srgb, var(--chip-primary, $gold) 45%, transparent),
        0 0 48px color-mix(in srgb, var(--chip-primary, $gold) 20%, transparent);
      animation: spotlight-pulse 1.8s ease-in-out infinite;
    }

    .mchip__bg {
      filter: brightness(1.15) contrast(1.05);
    }
  }

  &--eliminated {
    opacity: 0.35;
    filter: grayscale(0.8);
  }

  &--targetable {
    cursor: pointer;

    .mchip__frame {
      animation: target-pulse 1.2s ease-in-out infinite;
    }

    &.mchip--hovered .mchip__frame {
      box-shadow:
        0 0 0 2px $crimson-bright,
        0 0 24px rgba($crimson-bright, 0.4);
      animation: none;
    }
  }

  &--hovered {
    background: linear-gradient(180deg,
        color-mix(in srgb, var(--chip-accent, $gold-dark) 23%, transparent) 0%,
        color-mix(in srgb, var(--chip-primary, $gold) 17%, transparent) 50%,
        transparent 100%);

    .mchip__frame {
      box-shadow:
        0 8px 24px rgba(0, 0, 0, 0.18),
        0 0 0 1px rgba(255, 255, 255, 0.4) inset,
        0 0 16px rgba($gold, 0.15);
    }

    .mchip__bg {
      filter: brightness(1.1);
    }

    .mchip__inner-border {
      opacity: 0.7;
    }
  }
}

/*  Body  clips pattern, fades edges with gradient  */
.mchip__body {
  position: absolute;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
  mask-image: linear-gradient(180deg,
      black 0%,
      black 50%,
      transparent 100%);
  -webkit-mask-image: linear-gradient(180deg,
      black 0%,
      black 50%,
      transparent 100%);
}

/*  Repeating deity icon pattern  */
.mchip__pattern {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 200%;
  pointer-events: none;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mchip__pattern-row {
  display: flex;
  gap: 0.3rem;
  padding: 0.15rem 0;
  white-space: nowrap;

  &:nth-child(even) {
    transform: translateX(-16px);
  }
}

.mchip__pattern-icon {
  width: 28px;
  height: 28px;
  flex-shrink: 0;
  color: #fff;
  opacity: 0.4;

  svg {
    width: 100%;
    height: 100%;
    stroke-width: 2.5;
  }
}

/*  Frame  clips image, has border/shadow  */
.mchip__frame {
  position: relative;
  z-index: 1;
  width: 130px;
  height: 130px;
  border-radius: 10px;
  margin-top: 0.75rem;
  overflow: hidden;
  border: 1.25px solid var(--chip-primary, $gold);
  box-shadow:
    0 3px 14px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba(255, 255, 255, 0.3) inset;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  background: color-mix(in srgb, var(--chip-primary, $gold) 100%, #f5ede4);
}

/*  Inner gold frame glow  */
.mchip__inner-border {
  position: absolute;
  inset: 2px;
  border-radius: 8px;
  border: 1px solid rgba($gold, 0.25);
  pointer-events: none;
  z-index: 3;
  opacity: 0.4;
  transition: opacity 0.2s;
}

/*  Target crosshair badge  */
.mchip__target-badge {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 44px;
  height: 44px;
  z-index: 4;
  opacity: 0.6;
  color: $crimson-bright;
  filter: drop-shadow(0 0 6px rgba($crimson-bright, 0.6));
  animation: target-badge-in 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
  pointer-events: none;

  svg {
    width: 100%;
    height: 100%;
  }
}

@keyframes target-badge-in {
  from {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.8);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

/*  Full background image  */
.mchip__bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  transition: filter 0.2s ease;

  &--placeholder {
    display: flex;
    align-items: center;
    justify-content: center;
    background: color-mix(in srgb, var(--chip-primary, $gold) 15%, #f5ede4);
    color: $ink-faint;
    font-family: 'Cinzel', serif;
    font-size: 2rem;
    font-weight: 900;
  }
}

/*  Overlay: deity + name + HP bar  */
.mchip__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 0.45rem 0.5rem;
  background: linear-gradient(to top,
      rgba(0, 0, 0, 0.72) 0%,
      rgba(0, 0, 0, 0.35) 60%,
      transparent 100%);
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  z-index: 2;
}

.mchip__deity-tag {
  font-size: 0.4rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  letter-spacing: 0.08em;
}

.mchip__name {
  font-size: 0.75rem;
  font-weight: 700;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  line-height: 1.2;
}

.mchip__hp {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.mchip__hp-track {
  flex: 1;
  height: 3px;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
}

.mchip__hp-fill {
  height: 100%;
  border-radius: 2px;
  background: linear-gradient(90deg, darken($crimson, 10%), $crimson-bright 60%, #e63050);
  box-shadow: 0 0 6px rgba($crimson-bright, 0.5);
  transition: width 0.6s ease;
}

.mchip__hp-label {
  font-family: 'DM Mono', monospace;
  font-size: 0.4rem;
  color: #fff;
  white-space: nowrap;
}

/* 
   HOVER POPUP  detailed stat panel
    */
.mchip__popup {
  width: 220px;
  padding: 0.5rem;
  background:
    linear-gradient(180deg,
      color-mix(in srgb, var(--chip-accent, $gold-dark) 6%, rgba(255, 255, 255, 0.97)) 0%,
      rgba(255, 255, 255, 0.97) 100%);
  backdrop-filter: blur(16px);
  border: 1px solid color-mix(in srgb, var(--chip-primary, $gold) 25%, transparent);
  border-radius: 10px;
  box-shadow:
    0 12px 40px rgba(0, 0, 0, 0.14),
    0 0 0 1px color-mix(in srgb, var(--chip-primary, $gold) 8%, transparent);

  display: flex;
  flex-direction: column;
  gap: 0.35rem;
  z-index: 9999;
  pointer-events: all;

  &--visible {
    animation: bhud-popup-in 0.15s ease-out;
  }
}

@keyframes bhud-popup-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

/*  Popup header  */
.mchip__popup-header {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  padding-bottom: 0.25rem;
  border-bottom: 1px solid color-mix(in srgb, var(--chip-primary, $gold) 18%, transparent);
}

.mchip__popup-name {
  font-size: 0.7rem;
  font-weight: 700;
  color: color-mix(in srgb, var(--chip-accent, $gold-dark) 60%, $ink);
}

.mchip__popup-deity {
  font-size: 0.5rem;
  color: color-mix(in srgb, var(--chip-primary, $gold) 30%, $ink-faint);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/*  Stats rows  */
.mchip__stats {
  display: flex;
  flex-direction: column;
  background: color-mix(in srgb, var(--chip-primary, $gold) 4%, transparent);
  border: 1px solid color-mix(in srgb, var(--chip-primary, $gold) 10%, transparent);
  border-radius: 5px;
  overflow: hidden;
}

.mchip__stat {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.3rem 0.4rem;

  & + & {
    border-top: 1px solid color-mix(in srgb, var(--chip-primary, $gold) 8%, transparent);
  }
}

.mchip__stat-lbl {
  font-size: 0.4rem;
  font-weight: 600;
  color: $ink-faint;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.mchip__stat-val {
  font-weight: 700;
  font-size: 0.58rem;
  color: color-mix(in srgb, var(--chip-accent, $gold-dark) 70%, $ink);
}

/*  Skill orbs  */
.mchip__skills {
  display: flex;
  width: 80%;
  gap: 1rem;
  justify-content: space-between;
  margin: 3px auto 0;
}

.mchip__so {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.2rem;
}

.mchip__so--locked {
  opacity: 0.4;
}

.mchip__so-orb {
  position: relative;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background:
    radial-gradient(circle at 38% 32%,
      color-mix(in srgb, var(--chip-accent, $gold-dark) 14%, rgba(255, 255, 255, 0.95)),
      color-mix(in srgb, var(--chip-accent, $gold-dark) 4%, rgba(255, 255, 255, 0.95)) 65%,
      color-mix(in srgb, var(--chip-accent, $gold-dark) 10%, rgba(255, 255, 255, 0.95)));
  box-shadow: 0 0 0 1px color-mix(in srgb, var(--chip-primary, $gold) 12%, transparent);
}

.mchip__so-svg {
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
}

.mchip__so-track {
  fill: none;
  stroke: color-mix(in srgb, var(--chip-primary, $gold) 10%, transparent);
  stroke-width: 2.5;
}

.mchip__so-arc {
  fill: none;
  stroke: var(--chip-primary, $gold);
  stroke-width: 3;
  stroke-linecap: round;
  filter: drop-shadow(0 0 3px color-mix(in srgb, var(--chip-primary, $gold) 35%, transparent));
  transition: stroke-dashoffset 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.mchip__so-icon {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  margin: auto;
  color: color-mix(in srgb, var(--chip-accent, $gold-dark) 85%, $ink);
}

.mchip__so-label {
  font-size: 0.32rem;
  font-weight: 700;
  color: $ink-faint;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

/*  Powers list  */
.mchip__powers {
  display: flex;
  flex-direction: column;
  gap: 0.15rem;
}

.mchip__power {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  background: color-mix(in srgb, var(--chip-primary, $gold) 5%, transparent);
  border: 1px solid color-mix(in srgb, var(--chip-primary, $gold) 10%, transparent);
  border-radius: 5px;
  padding: 0.35rem;
}

.mchip__power-icon {
  font-size: 0.6rem;
  flex-shrink: 0;
  width: 16px;
  text-align: center;
  color: color-mix(in srgb, var(--chip-accent, $gold-dark) 50%, $ink-mid);
}

.mchip__power-info {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.mchip__power-tag {
  font-size: 0.32rem;
  font-weight: 700;
  color: color-mix(in srgb, var(--chip-primary, $gold) 25%, $ink-faint);
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.mchip__power-name {
  font-size: 0.56rem;
  color: $ink-mid;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*  Target pulse animation  */
@keyframes target-pulse {
  0%, 100% {
    box-shadow:
      0 0 0 1px rgba($crimson-bright, 0.3),
      0 0 8px rgba($crimson-bright, 0.1);
  }
  50% {
    box-shadow:
      0 0 0 2px rgba($crimson-bright, 0.5),
      0 0 18px rgba($crimson-bright, 0.25);
  }
}

/*  Spotlight pulse animation  */
@keyframes spotlight-pulse {
  0%, 100% {
    box-shadow:
      0 0 0 2px var(--chip-primary, $gold),
      0 0 24px color-mix(in srgb, var(--chip-primary, $gold) 45%, transparent),
      0 0 48px color-mix(in srgb, var(--chip-primary, $gold) 20%, transparent);
  }
  50% {
    box-shadow:
      0 0 0 2.5px var(--chip-primary, $gold),
      0 0 32px color-mix(in srgb, var(--chip-primary, $gold) 55%, transparent),
      0 0 64px color-mix(in srgb, var(--chip-primary, $gold) 30%, transparent);
  }
}

/* 
   RESPONSIVE
    */
@media (max-width: $bp-medium) {
  .mchip {
    width: 100px;
  }

  .mchip__frame {
    width: 100px;
    height: 100px;
    border-radius: 8px;
  }

  .mchip__inner-border {
    border-radius: 6px;
  }

  .mchip__name {
    font-size: 0.55rem;
  }

  .mchip__hp-label {
    font-size: 0.36rem;
  }

  .mchip__deity-tag {
    font-size: 0.36rem;
  }

  .mchip__popup {
    width: 190px;
    padding: 0.4rem;
  }
}

@media (max-width: $bp-compact) {
  .mchip {
    width: 90px;
    min-width: unset;
    align-items: center;
    background: linear-gradient(90deg,
      color-mix(in srgb, var(--chip-accent, $gold-dark) 14%, transparent) 0%,
      color-mix(in srgb, var(--chip-primary, $gold) 8%, transparent) 50%,
      transparent 100%);

    &.mchip--hovered {
      background: linear-gradient(90deg,
        color-mix(in srgb, var(--chip-accent, $gold-dark) 13%, transparent) 0%,
        color-mix(in srgb, var(--chip-primary, $gold) 17%, transparent) 50%,
        transparent 100%);
    }
  }

  .mchip__body {
    -webkit-mask-image: linear-gradient(180deg, black 0%, black 50%, transparent 100%),
      linear-gradient(90deg, black 0%, black 50%, transparent 100%);
    mask-image: linear-gradient(180deg, black 0%, black 50%, transparent 100%),
      linear-gradient(90deg, black 0%, black 50%, transparent 100%);
    -webkit-mask-composite: destination-in;
    mask-composite: intersect;
  }

  .mchip__frame {
    width: 70px;
    height: 70px;
    border-width: 0.5px !important;
    border-radius: 7px;
    margin: auto 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    cursor: pointer;
  }

  .mchip__inner-border {
    border-width: 0.5px;
    border-radius: 5px;
  }

  .mchip__name {
    font-size: 0.42rem;
  }

  .mchip__hp-label {
    display: none;
  }

  .mchip__deity-tag {
    display: none;
  }

  .mchip__overlay {
    padding: 0 0.3rem 0.35rem;
  }

  .mchip__popup {
    padding: 0.4rem;
  }

  .mchip__skills {
    width: 100%;
    justify-content: center;
    gap: 0.5rem;
  }

  /*  Compact battle states  thinner borders & smaller glows  */
  .mchip--attacker .mchip__frame {
    box-shadow:
      0 0 0 1px var(--chip-primary, $gold),
      0 0 10px color-mix(in srgb, var(--chip-primary, $gold) 25%, transparent);
  }

  .mchip--defender .mchip__frame {
    box-shadow:
      0 0 0 1px $crimson-bright,
      0 0 8px rgba($crimson-bright, 0.25);
  }

  .mchip--spotlight .mchip__frame {
    box-shadow:
      0 0 0 1px var(--chip-primary, $gold),
      0 0 12px color-mix(in srgb, var(--chip-primary, $gold) 35%, transparent),
      0 0 24px color-mix(in srgb, var(--chip-primary, $gold) 15%, transparent);
    animation: spotlight-pulse-compact 1.8s ease-in-out infinite;
  }

  .mchip--targetable .mchip__frame {
    animation: target-pulse-compact 1.2s ease-in-out infinite;
  }

  .mchip--targetable.mchip--hovered .mchip__frame {
    box-shadow:
      0 0 0 1px $crimson-bright,
      0 0 12px rgba($crimson-bright, 0.3);
    animation: none;
  }
}

/*  Compact keyframe variants  */
@keyframes target-pulse-compact {
  0%, 100% {
    box-shadow:
      0 0 0 0.5px rgba($crimson-bright, 0.3),
      0 0 4px rgba($crimson-bright, 0.1);
  }
  50% {
    box-shadow:
      0 0 0 1px rgba($crimson-bright, 0.5),
      0 0 10px rgba($crimson-bright, 0.2);
  }
}

@keyframes spotlight-pulse-compact {
  0%, 100% {
    box-shadow:
      0 0 0 1px var(--chip-primary, $gold),
      0 0 12px color-mix(in srgb, var(--chip-primary, $gold) 35%, transparent),
      0 0 24px color-mix(in srgb, var(--chip-primary, $gold) 15%, transparent);
  }
  50% {
    box-shadow:
      0 0 0 1.5px var(--chip-primary, $gold),
      0 0 16px color-mix(in srgb, var(--chip-primary, $gold) 45%, transparent),
      0 0 32px color-mix(in srgb, var(--chip-primary, $gold) 22%, transparent);
  }
}