@use '../../../styles/variables' as *;

/*  D10 Canvas (3D)  */
.dr__d10-canvas {
  width: 320px;
  height: 320px;
  position: relative;
  cursor: pointer;
  background: transparent;
  animation: dr-float 3s ease-in-out infinite;

  &::after {
    content: '';
    position: absolute;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    height: 35px;
    border-radius: 50%;
    background: radial-gradient(ellipse,
        color-mix(in srgb, var(--ci-fg, #000) 16%, transparent),
        transparent 70%);
    transition: all 0.4s ease;
  }

  &:hover {
    animation-play-state: paused;
  }

  > div {
    width: 100% !important;
    height: 100% !important;
  }

  canvas {
    display: block;
    width: 100% !important;
    height: 100% !important;
  }
}

/*  Wink sparkles on settle  */
.dr__d10-wink {
  position: absolute;
  pointer-events: none;

  &::before,
  &::after {
    content: '';
    position: absolute;
    border-radius: 2px;
  }

  &::before {
    top: 0;
    left: 50%;
    width: 3px;
    height: 100%;
    transform: translateX(-50%);
  }

  &::after {
    top: 50%;
    left: 0;
    width: 100%;
    height: 3px;
    transform: translateY(-50%);
  }

  &--1 {
    top: 25%; left: 5%; width: 18px; height: 18px;
    z-index: -1; filter: blur(7px);
    animation: d10Wink 0.7s ease-out forwards;
    animation-delay: 0s;
    &::before, &::after { background: rgba(255, 230, 200, 0.9); }
  }

  &--2 {
    top: 18%; right: 6%; left: auto; width: 12px; height: 12px;
    z-index: -1; filter: blur(7px);
    animation: d10Wink 0.6s ease-out forwards;
    animation-delay: 0.1s;
    &::before, &::after { background: rgba(200, 230, 255, 0.8); }
  }

  &--3 {
    top: 65%; left: 8%; width: 10px; height: 10px;
    z-index: -1; filter: blur(7px);
    animation: d10Wink 0.55s ease-out forwards;
    animation-delay: 0.18s;
    &::before, &::after { background: rgba(255, 200, 230, 0.8); }
  }

  &--4 {
    top: 58%; right: 4%; left: auto; width: 14px; height: 14px;
    z-index: -1; filter: blur(7px);
    animation: d10Wink 0.7s ease-out forwards;
    animation-delay: 0.06s;
    &::before, &::after { background: rgba(220, 255, 200, 0.8); }
  }

  &--5 {
    top: 8%; left: 42%; width: 10px; height: 10px;
    z-index: -1; filter: blur(7px);
    animation: d10Wink 0.55s ease-out forwards;
    animation-delay: 0.14s;
    &::before, &::after { background: rgba(220, 210, 255, 0.8); }
  }

  &--6 {
    top: 38%; left: 22%; width: 6px; height: 6px;
    z-index: 10; filter: blur(7px);
    animation: d10Wink 0.45s ease-out forwards;
    animation-delay: 0.04s;
    &::before { width: 1.5px; }
    &::after { height: 1.5px; }
    &::before, &::after { background: rgba(255, 255, 255, 0.95); }
  }

  &--7 {
    top: 30%; right: 20%; left: auto; width: 5px; height: 5px;
    z-index: 10; filter: blur(7px);
    animation: d10Wink 0.4s ease-out forwards;
    animation-delay: 0.12s;
    &::before { width: 1.5px; }
    &::after { height: 1.5px; }
    &::before, &::after { background: rgba(255, 255, 255, 0.9); }
  }

  &--8 {
    top: 52%; left: 48%; width: 4px; height: 4px;
    z-index: 10; filter: blur(7px);
    animation: d10Wink 0.35s ease-out forwards;
    animation-delay: 0.08s;
    &::before { width: 1px; }
    &::after { height: 1px; }
    &::before, &::after { background: rgba(255, 255, 255, 1); }
  }
}

@keyframes d10Wink {
  0% { transform: scale(0) rotate(0deg); opacity: 0; }
  12% { transform: scale(1.6) rotate(15deg); opacity: 1; }
  35% { transform: scale(1.1) rotate(30deg); opacity: 0.9; }
  100% { transform: scale(0) rotate(50deg); opacity: 0; }
}
