@use '../../../../styles/variables' as *;

$gold: #c9a84c;
$gold-dark: #a8872a;
$ink: #2c2218;
$ink-mid: #6b5d4a;
$ink-faint: #9a8b76;
$crimson: #a0001b;
$crimson-bright: #c41e3a;

.bhud {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 60;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.6rem 0.8rem;
  height: 7rem;
  pointer-events: none;

  >* {
    pointer-events: all;
  }
}

/*  Top bar: round + turn info  */
.bhud__bar {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  background: rgba(255, 255, 255, 0.92);
  backdrop-filter: blur(12px);
  border: 1px solid rgba($gold, 0.15);
  border-radius: 8px;
  padding: 0.4rem 0.7rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  z-index: 100;
}

.bhud__round {
  font-size: 0.55rem;
  font-weight: 700;
  color: $gold-dark;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  white-space: nowrap;
}

.bhud__turn-info {
  display: flex;
  align-items: baseline;
  gap: 0.35rem;
  min-width: 0;
}

.bhud__attacker-name {
  font-weight: 700;
  font-size: 0.7rem;
  color: $ink;
  white-space: nowrap;
}

.bhud__phase-label {
  font-size: 0.65rem;
  color: $ink-faint;
}

/*  Target selection modal  */
.bhud__targets-modal {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.97);
  border: 1px solid rgba($gold, 0.2);
  border-radius: 10px;
  padding: 0.7rem 0.7rem 0.6rem;
  box-shadow:
    0 12px 36px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba($gold, 0.08);
  animation: bhud-scale-in 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
  width: 280px;
  max-height: calc(100vh - 6rem);
  overflow-y: auto;
}

.bhud__targets-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  width: 100%;
}

.bhud__target-btn {
  --tc: var(--t-color, #{$gold});
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.4rem 0.55rem;
  background: color-mix(in srgb, var(--tc) 6%, transparent);
  border: 1px solid color-mix(in srgb, var(--tc) 18%, transparent);
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.15s, border-color 0.15s, box-shadow 0.15s;
  width: 100%;

  &:hover {
    background: color-mix(in srgb, var(--tc) 16%, transparent);
    border-color: color-mix(in srgb, var(--tc) 35%, transparent);
    box-shadow: 0 0 12px color-mix(in srgb, var(--tc) 20%, transparent);
  }
}

.bhud__target-img {
  width: 36px;
  height: 36px;
  border-radius: 5px;
  object-fit: cover;
  flex-shrink: 0;
  border: 2px solid color-mix(in srgb, var(--tc) 30%, transparent);
}

.bhud__target-initial {
  width: 36px;
  height: 36px;
  border-radius: 5px;
  background: color-mix(in srgb, var(--tc) 12%, #f5ede4);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--tc);
  flex-shrink: 0;
}

.bhud__target-info {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
  min-width: 0;
}

.bhud__target-name {
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--tc);
}

.bhud__target-hp {
  width: fit-content;
  font-size: 0.55rem;
  color: $ink-faint;
  font-family: 'DM Mono', monospace;
}

/* 
   DICE ROLLING  positioned on player's half
    */
.bhud__dice-zone {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  pointer-events: none;
  animation: bhud-fade-in 0.2s ease-out;

  // Left half: from sidebar to content center
  &--left {
    left: $sidebar-width;
    width: calc((100vw - #{$sidebar-width}) / 2);
  }

  // Right half: from content center to right edge
  &--right {
    right: 0;
    width: calc((100vw - #{$sidebar-width}) / 2);
  }
}

@keyframes bhud-fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.bhud__dice-modal {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
  background: rgba(255, 255, 255, 0.97);
  border: 1px solid rgba($gold, 0.2);
  border-radius: 10px;
  padding: 0.5rem 0.4rem 0.3rem;
  box-shadow:
    0 12px 36px rgba(0, 0, 0, 0.12),
    0 0 0 1px rgba($gold, 0.08);
  animation: bhud-scale-in 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
  width: 220px;
  margin-top: 2rem;
  max-height: calc(100vh - 6rem);
  overflow: visible;
}

@keyframes bhud-scale-in {
  from {
    opacity: 0;
    transform: scale(0.92);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.bhud__dice-label {
  font-size: 0.65rem;
  font-weight: 800;
  color: $ink;
  text-transform: uppercase;
  letter-spacing: 0.08em;
}

.bhud__dice-sub {
  font-size: 0.55rem;
  color: $ink-faint;
  margin-top: -2px;
  margin-bottom: 2px;
}

/* DiceRoller embedded inside modal  override default sizes */
.bhud__dice-roller {
  width: 100%;
  height: 180px;
  border-radius: 0px;
  overflow: hidden;

  .dr__display {
    min-height: unset;
    padding-bottom: 0;
    gap: 6px;
    transform: scale(0.55);
    transform-origin: center center;

    &::after {
      width: 400px;
      height: 400px;
    }
  }

  .dr__prompt {
    display: none;
  }

  .dr__result {
    font-size: 1.2rem;
    padding: 0.15rem 0.4rem;
  }
}

.bhud__dice-bonus {
  font-size: 0.65rem;
  font-weight: 700;
  color: $gold-dark;
  font-family: 'DM Mono', monospace;
  margin: 1px 0 2px;
}

/*  Waiting spinner (used inside dice modal)  */
.bhud__dice-roller--waiting {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bhud__roll-waiting-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid rgba($gold, 0.15);
  border-top-color: $gold;
  border-radius: 50%;
  animation: bhud-spin 0.8s linear infinite;
}

@keyframes bhud-spin {
  to {
    transform: rotate(360deg);
  }
}

/*  Roll result card (opponent's result)  */
.bhud__roll-result {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
  background: rgba(255, 255, 255, 0.97);
  border: 1px solid rgba($gold, 0.2);
  border-radius: 12px;
  padding: 1rem 1.5rem;
  box-shadow:
    0 12px 36px rgba(0, 0, 0, 0.10),
    0 0 0 1px rgba($gold, 0.08);
  animation: bhud-scale-in 0.2s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.bhud__roll-result-label {
  font-size: 0.6rem;
  font-weight: 700;
  color: $ink-faint;
  text-transform: uppercase;
  letter-spacing: 0.08em;
}

.bhud__roll-result-name {
  font-size: 0.8rem;
  font-weight: 800;
  color: $ink;
}

.bhud__roll-result-val {
  font-size: 2.5rem;
  font-weight: 900;
  color: $ink;
  font-family: 'DM Mono', monospace;
  line-height: 1;
  margin: 0.2rem 0;
}

.bhud__roll-result-bonus {
  font-size: 0.6rem;
  font-weight: 600;
  color: $gold-dark;
}

/*  Resolve phase  */
.bhud__resolve {
  position: absolute;
  bottom: 100%;
  left: 0.8rem;
  right: 0.8rem;
  margin-bottom: 0.3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(12px);
  border: 1px solid rgba($crimson, 0.2);
  border-radius: 8px;
  padding: 0.5rem 0.7rem;
  box-shadow: 0 4px 20px rgba($crimson, 0.08);
  z-index: 61;

  &--miss {
    border-color: rgba($gold, 0.2);
    box-shadow: 0 4px 20px rgba($gold, 0.08);
  }

  &--exit {
    animation: bhud-slide-out 0.25s ease-out forwards;
    pointer-events: none;
  }
}

.bhud__resolve-info {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.bhud__resolve-rolls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.bhud__resolve-roll {
  display: flex;
  align-items: center;
  gap: 0.2rem;
}

.bhud__resolve-roll-label {
  font-size: 0.4rem;
  font-weight: 700;
  color: $ink-faint;
  text-transform: uppercase;
}

.bhud__resolve-roll-val {
  font-size: 0.65rem;
  font-weight: 800;
  color: $ink;
  font-family: 'DM Mono', monospace;
}

.bhud__resolve-roll-bonus {
  font-size: 0.45rem;
  font-weight: 600;
  color: $gold-dark;
}

.bhud__resolve-roll-total {
  font-size: 0.5rem;
  font-weight: 700;
  color: $ink-mid;
}

.bhud__resolve-vs {
  font-size: 0.4rem;
  font-weight: 700;
  color: $ink-faint;
  text-transform: uppercase;
}

.bhud__resolve-dmg {
  font-size: 0.6rem;
  font-weight: 800;
  color: $crimson-bright;
}

.bhud__resolve-miss {
  font-size: 0.6rem;
  font-weight: 800;
  color: $gold-dark;
}

.bhud__resolve-btn {
  font-size: 0.5rem;
  font-weight: 700;
  color: #fff;
  background: $gold-dark;
  border: none;
  border-radius: 5px;
  padding: 0.3rem 0.6rem;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  transition: background 0.15s;

  &:hover {
    background: $gold;
  }
}

/*  Waiting  */
.bhud__waiting {
  position: absolute;
  bottom: 100%;
  left: 0.8rem;
  right: 0.8rem;
  margin-bottom: 0.3rem;
  text-align: center;
  font-size: 0.6rem;
  color: $ink-faint;
  padding: 0.4rem;
  background: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(8px);
  border-radius: 8px;
  border: 1px solid rgba($gold, 0.1);
  z-index: 61;

  &--exit {
    animation: bhud-slide-out 0.25s ease-out forwards;
    pointer-events: none;
  }
}

@keyframes bhud-slide-out {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-8px);
  }
}

/*  Result badges (winner / loser)  */
.bhud__result-badge {
  pointer-events: all;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.4rem;
  padding: 1.2rem 1.5rem;
  text-align: center;
  opacity: 0.5;
  backdrop-filter: blur(16px);
  animation: bhud-scale-in 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);

  &--winner {
    .bhud__result-icon {
      color: $gold;
    }

    .bhud__result-label {
      color: $gold-dark;
    }
  }

  &--loser {

    .bhud__result-icon {
      color: $ink-faint;
    }

    .bhud__result-label {
      color: $ink-faint;
    }
  }
}

.bhud__result-icon {
  width: 50%;
  height: 50%;
}

.bhud__result-label {
  font-size: 1rem;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.12em;
}

.bhud__result-names {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.15rem;
  font-size: 0.75rem;
  font-weight: 700;
  color: $ink-mid;
}

/*  Winner bottom bar  */
.bhud__winner {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(16px);
  border: 1px solid rgba($gold, 0.25);
  border-radius: 10px;
  box-shadow: 0 8px 32px rgba($gold, 0.12);
  text-align: center;
}

.bhud__winner-label {
  font-size: 0.5rem;
  font-weight: 700;
  color: $gold-dark;
  text-transform: uppercase;
  letter-spacing: 0.12em;
}

.bhud__winner-name {
  font-size: 1rem;
  font-weight: 800;
  color: $ink;
}

/*  Battle log  */
.bhud__log {
  position: relative;
  z-index: 60;
  display: flex;
  flex-direction: column;
  gap: 0.15rem;
  max-height: 100px;
  overflow-y: auto;
  flex: 1;
}

.bhud__log-empty {
  font-size: 0.6rem;
  color: darken($ink-faint, 0.5);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.3rem;
  opacity: 0.5;
  flex: 1;
  min-height: 100%;
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.25) 0%, transparent 70%);
}

.bhud__log-entry {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  font-size: 0.75rem;
  color: $ink-mid;
  padding: 0.35rem 0.7rem;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
  z-index: 100;
}

.bhud__log-round {
  font-weight: 700;
  color: $gold-dark;
  font-size: 0.55rem;
}

.bhud__log-name {
  font-weight: 700;
  color: $ink;
}

.bhud__log-dice {
  font-weight: 800;
  color: $ink;
  font-family: 'DM Mono', monospace;
}

.bhud__log-bonus {
  font-size: 0.6rem;
  font-weight: 600;
  color: $gold-dark;
  font-family: 'DM Mono', monospace;
}

.bhud__log-vs {
  font-size: 0.6rem;
  font-weight: 600;
  color: $ink-faint;
  text-transform: uppercase;
}

.bhud__log-sep {
  color: $ink-faint;
}

@media (max-width: $bp-compact) {
  .bhud__log-entry {
    flex-wrap: wrap;
    gap: 0.15rem 0.3rem;
  }

  .bhud__log-sep {
    flex-basis: 100%;
    height: 0;
    overflow: hidden;
    font-size: 0;
    line-height: 0;
  }
}

.bhud__log-hit {
  font-weight: 800;
  color: $crimson-bright;
}

.bhud__log-ko {
  font-weight: 800;
  color: $crimson;
  font-size: 0.6rem;
}

/*  Responsive  */
@media (max-width: $bp-medium) {
  .bhud {
    padding: 0.4rem 0.5rem;
  }

  .bhud__bar {
    padding: 0.3rem 0.5rem;
  }

  .bhud__targets-modal {
    width: 220px;
  }

  .bhud__dice-modal {
    width: 180px;
  }

  .bhud__dice-roller {
    height: 150px;

    .dr__display {
      transform: scale(0.4);
      transform-origin: center center;
    }
  }

  /* Sidebar hidden  remove offset */
  .bhud__dice-zone--left {
    left: 0;
    width: 50vw;
  }

  .bhud__dice-zone--right {
    width: 50vw;
  }
}

@media (max-width: $bp-compact) {

  /* Log + bar as normal flex item so halves don't include its height */
  .bhud {
    position: relative;
    bottom: auto;
    left: auto;
    right: auto;
    flex-shrink: 0;
    height: 5.5rem;
    padding: 0.3rem 0.5rem;
  }

  .bhud__resolve,
  .bhud__waiting {
    left: 0.5rem;
    right: 0.5rem;
  }

  .bhud__log {
    max-height: 60px;
  }

  .bhud__dice-modal {
    width: 150px;
    padding: 0.35rem 0.3rem 0.25rem;
    border-radius: 8px;
    gap: 0.2rem;
  }

  .bhud__dice-label {
    font-size: 0.5rem;
  }

  .bhud__dice-sub {
    font-size: 0.45rem;
  }

  .bhud__dice-roller {
    height: 110px;

    .dr__display {
      transform: scale(0.3);
    }

    .dr__result {
      font-size: 0.85rem;
      padding: 0.1rem 0.25rem;
    }
  }

  .bhud__dice-bonus {
    font-size: 0.5rem;
  }

  .bhud__roll-result {
    padding: 0.6rem 0.8rem;
    border-radius: 8px;
  }

  .bhud__roll-result-val {
    font-size: 1.8rem;
  }

  .bhud__roll-result-name {
    font-size: 0.65rem;
  }

  /* Dice zones: each half = (100vh - topbar - bhud) / 2 */
  $compact-half: calc(50vh - #{$topbar-height} - 2.75rem);

  .bhud__dice-zone--left {
    left: auto;
    right: 0;
    top: calc(2 * #{$topbar-height});
    bottom: auto;
    height: #{$compact-half};
    width: calc(100vw - 90px);
    padding-right: 10px;

    .bhud__dice-modal,
    .bhud__targets-modal {
      margin-top: -1rem;
    }
  }

  .bhud__dice-zone--right {
    left: auto;
    right: 0;
    top: calc(2 * #{$topbar-height} + #{$compact-half});
    bottom: auto;
    height: #{$compact-half};
    width: calc(100vw - 90px);
    padding-right: 10px;

    .bhud__dice-modal,
    .bhud__targets-modal {
      margin-top: 0;
    }
  }
}